import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Obj_logo from "../images/LOGO_OBJ.svg";
import React, { useState, useEffect } from 'react';

function CollapsibleExample() {

  
  return (
    <Navbar collapseOnSelect expand="lg" className='nav_bg' variant="dark">
      <Container>
        <Navbar.Brand href="/" >
          <div className='d-flex brand_logo'>
            <img className='Obw_logo' src={Obj_logo} alt="BigCo Inc. logo" />
          </div>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto"></Nav>
          <Nav>
            <Nav.Link href="/" className='nav_font home_one drophover'>HOME</Nav.Link>
            {/* <Nav.Link href="/" className='nav_font home_one drophover'>Knowledge base</Nav.Link> */}
            <Nav.Link href="/" className='nav_font home_one drophover'>SUBMIT A TICKET</Nav.Link>
            <Nav.Link href="/login" className='nav_font home_one drophover'>ADMIN'S LOGIN</Nav.Link>
            {/* <NavDropdown title="Dashboard" id="collasible-nav-dropdown" className='nav_font dropmain drophover'>
              <NavDropdown.Item href="/login" className='drophover'>IT Admin Login</NavDropdown.Item>
              <NavDropdown.Item href="/loginTC" className='drophover'>Facility Admin Login</NavDropdown.Item>
              <NavDropdown.Item href="/loginHR" className='drophover '>HR Admin Login</NavDropdown.Item>
            </NavDropdown> */}

          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default CollapsibleExample;
