import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import Navbar from "./navbarPage";

const ViewPage = () => {
  const location = useLocation();
  const [employeeData, setEmployeeData] = useState(null);

  useEffect(() => {
    if (location.state && location.state.employeeData) {
      setEmployeeData(location.state.employeeData);
    }
  }, [location.state]);

  return (
<div>
<Navbar />
    <div className="container view-page">
       
      <h2 className='d-flex justify-content-center color font_header1 mb-4 clr-2'>View Page</h2>
      {employeeData ? (
        <table className="table table-bordered view_table">
          <tbody> 
            <tr>
              <th className='th clr-2'>Employee ID:</th>
              <td>{employeeData.employeeId}</td>
            </tr>
            <tr>
              <th className='th clr-2'>Employee Name:</th>
              <td>{employeeData.employeeName}</td>
            </tr>
            <tr>
              <th className='th clr-2'>Team Name:</th>
              <td>{employeeData.teamName}</td>
            </tr>
            <tr>
              <th className='th clr-2'>Team Manager:</th>
              <td>{employeeData.teamManager}</td>
            </tr>
            <tr>
              <th className='th clr-2'>Email ID:</th>
              <td>{employeeData.emailId}</td>
            </tr>
            <tr>
              <th className='th clr-2'>System No:</th>
              <td>{employeeData.systemNo}</td>
            </tr>
            <tr>
              <th className='th clr-2'>Requirements:</th>
              <td>{employeeData.systemType}</td>
            </tr>
            <tr>
              <th className='th clr-2'>Issue:</th>
              <td>{employeeData.systemTypetwo}</td>
            </tr>
            <tr>
              <th className='th clr-2'>Description:</th>
              <td>{employeeData.description}</td>
            </tr>
            <tr>
              <th className='th clr-2'>Location:</th>
              <td>{employeeData.location}</td>
            </tr>
            </tbody>
        </table>
      ) : (
        <p>No employee data available.</p>
      )}
    </div>
    </div>
  );
};

export default ViewPage;
